*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container{
    width: 1200px;
    margin: 0 auto;
}

.navbar{
    width: 100%;
    height: 70px;
    box-shadow: 0 5px 20px 2px rgba(0, 0, 0, 0.2);
    position: fixed;
    top: 0;
    background: #fff;
    z-index: 1;
}

.nav__container{
    width: 100%;
    display: flex;
    align-items: center;
    height: 70px;
    justify-content: space-between;
}

.img-logo{
    width: 70px;
    height: 70px;
    background-size: cover;
}

.mt-100{
    margin-top: 100px;
}

.header{
    background-image: url('https://www.apple.com/v/environment/m/images/meta/og__bghpqp9r7ewi.png?202105280658');
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    object-fit: contain;
    background-size: cover;
}

a{
    text-decoration: none;
}

.icon{
    color: rgb(243, 126, 126);
    font-size: 22px;
}

.basket{
    position: relative;
}

.basket span{
    display: flex;
    position: absolute;
    align-items: center;
    justify-content: center;
    right: -18px;
    top: 0;
    width: 25px;
    height: 25px;
    background: rgb(66, 64, 64);
    border-radius: 50px;
    color: white;
    font-size: 12px;
}

/* .img{
    width: 100%;
    height: 230px;
    object-fit: contain;
} */

.mtb-30{
    margin-top: 30px;
    margin-bottom: 30px;
}

.row{
    display: flex;
    width: 100%;
    flex-wrap: wrap;
}

.col-2{
    width: 16.33333333333333%;
    padding: 20px;
}

.col-3{
    width: 25%;
}

.col-9{
    width: 75%;
}

.col-6{
    width: 50%;
}

.product{
    padding: 25px;
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
}

.product-img img{
    width: 100%;
    height: 230px;
    object-fit: contain;
    transition: all 0.5s linear;
}

.product-img img:hover{
    transform: scale(1.4);
}

.product__name{
    margin: 10px 0;
    text-transform: capitalize;
    color: #707070;
}

.product__price .discount{
    color: silver;
    font-weight: 400;
    font-size: 18px;
}

.actualPrice{
    text-decoration: line-through;
}

.product__discount__price{
    text-align: right;
}

.detail__image{
    width: 100%;
    height: auto;
    padding-right: 30px;
}

.detail__image img{
    width: 550px;
    height: auto;
    margin-bottom: 25px;
}

.detail__name{
    font-size: 28px;
    color: #2c2c2c;
    font-weight: 400;
}

.detail__price{
    margin: 20px 0;
}

.detail__actual-price{
    margin-left: 10px;
    text-decoration: line-through;
    font-size: 20px;
}

.detail__discount-price{
    margin-left: 10px;
    font-size: 20px;
}

.detail__wrapper{
    display: flex;
}

.increase,
.decrease{
    cursor: pointer;
}

.decrease:hover{
    background-color: rgb(170, 198, 209);
}

.increase:hover{
    background-color: rgb(170, 198, 209);
}

.decrease,
.increase,
.quantity{
    width: 60px;
    height: 60px;
    border: 1px silver solid;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column-reverse;
}

.quantity{
    border-right: none;
    border-left: none;
}

.add-btn{
    width: 180px;
    height: 50px;
    font-size: 18px;
    margin-top: -60px;
    margin-left: 150px;
    border: none;
    color: rgb(228, 220, 220);
    background: rgb(218, 138, 84);
    text-transform: uppercase;
    cursor: pointer;
    outline: none;
}

.add-btn:hover{
    border: 2px solid rgb(229, 215, 138);
}

.detail__text{
    margin-top: 20px;
}

.detail__text h3{
    font-weight: 600;
    margin-bottom: 10px;
    font-size: 18px;
}

/* cart */
.cart{
    margin-top: 150px;
}

.cart__heading{
background: #948f8f;
}

.cart__img img{
    width: 100px;
    height: 100px;
    object-fit: contain;
}
.vertical{
    align-items: center;
}

.text{
    text-align: center;
}

.cart__remove{
    cursor: pointer;
}

.summary{
    padding-left: 10px;
}

.summary__heading{
    background: #f4f6f8;
}

.summary__details{
    background: rgb(231, 130, 130);
    color: white;
    padding: 15px;
}

.mb-10{
    margin-bottom: 10px;
}

.checkout{
    border: none;
    cursor: pointer;
    outline: none;
    background: white;
    color: rgb(231, 130, 130);
    width: 100%;
    padding: 12px 5px;
    font-size: 17px;
    text-transform: uppercase;
}

.hel{
    margin-bottom: 50px;
}

.back_home span{
    text-transform: capitalize;
    border: 1px solid gray;
    padding: 5px 10px;
    margin-top: 130px;
    background: rgb(204, 152, 152);
}